html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #f0f0f0;
}

.contain {
  width: 100%;
  margin: 0 auto;
  max-width: 120rem;
}

.circle {
  background-color: #f0f0f0;
  position: relative;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  border: 2rem solid #222224;
}

p, h2 {
  font-family: "Rajdhani", sans-serif;
  font-weight: 700;
}

main {
  background-color: #c9c9c9;
}

.header {
  background-color: #ff0000;
  border-bottom: 2rem solid #222224;
}
.header .contain {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
}
.header .logo {
  display: flex;
}
.header .logo img {
  width: 14rem;
}
.header .logo .logo_text {
  font-family: "Rajdhani", sans-serif;
  font-size: 4.5rem;
  color: #222224;
  margin: 0;
  text-decoration: underline;
}
.header .circle {
  left: 1.3rem;
  top: 4.3rem;
}
.header .nav {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.header .nav a {
  font-size: 2rem;
  color: #222224;
  text-decoration: underline 2.5px #FFDE00;
  font-family: "Rajdhani", sans-serif;
  padding: 1.4rem;
  font-weight: 700;
}
.header .nav a:hover {
  background-color: #cc0000;
  color: #FFDE00;
  transition: all 0.5s;
}

main {
  padding: 2rem 0;
}

.interfaz {
  display: flex;
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  border-radius: 5px;
  align-items: center;
  height: 60.5rem;
}
.interfaz .pokeBuscar {
  display: flex;
  justify-content: center;
  width: 80%;
  margin-bottom: 2rem;
  height: auto;
}
.interfaz .pokeBuscar input {
  flex: 3;
  border: 5px solid #222224;
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  padding-left: 1rem;
}
.interfaz .pokeBuscar button {
  flex: 1;
  border: none;
}
.interfaz .btnRandom, .interfaz button {
  background-color: #222224;
  color: #f0f0f0;
  font-family: "Rajdhani", sans-serif;
  font-weight: 600;
  font-size: 3rem;
  padding: 1rem 2rem;
  cursor: pointer;
}
.interfaz .btnRandom:hover, .interfaz button:hover {
  background-color: #202021;
  color: #FFDE00;
}
.interfaz .btnRandom {
  width: 80%;
}

.pokeInfo {
  min-height: 29.2rem;
  width: 80%;
  display: flex;
  border: 1rem solid #222224;
  margin-bottom: 2rem;
}
.pokeInfo .sprite {
  flex: 1;
  display: flex;
  border-right: 1rem solid #222224;
}
.pokeInfo .sprite img {
  width: 20rem;
  margin: auto;
  -webkit-animation: mov 0.6s both;
  animation: mov 0.6s both;
}
.pokeInfo .info {
  flex: 1;
}
.pokeInfo .info .poke_info {
  background-color: #222224;
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5rem solid #FFDE00;
}
.pokeInfo .info .poke_info .poke_nombre, .pokeInfo .info .poke_info .poke_id {
  color: #f0f0f0;
  padding: 0.5rem 2rem;
}
.pokeInfo .info .poke_info .poke_nombre {
  text-transform: uppercase;
  margin: auto 0;
  font-size: 2.4rem;
}
.pokeInfo .info .poke_info .poke_id {
  font-size: 3.2rem;
  margin: 0;
}
.pokeInfo .tipo {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.pokeInfo .tipo .poke_tipo {
  text-align: center;
  width: 12rem;
  padding: 0.5rem 2rem;
  border: 0.3rem solid #222224;
  text-transform: uppercase;
  border-radius: 5px;
}
.pokeInfo .habilites {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  padding: 1.5rem 0;
  gap: 1.5rem;
  background-color: #3B4CCA;
}
.pokeInfo .habilites .poke_habilidad {
  margin: 0;
  text-transform: uppercase;
  background-color: #f0f0f0;
  text-align: center;
  width: 90%;
  padding: 0.5rem;
  border-radius: 5px;
}
.pokeInfo .habilites .poke_habilidad:hover {
  background-color: #FFDE00;
}

@-webkit-keyframes mov {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  25% {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  50% {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
@keyframes mov {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  25% {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  50% {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}
.footer {
  background-color: #f0f0f0;
  border-top: 2rem solid #222224;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  align-items: center;
}
.footer .circle {
  top: -3rem;
}